        background:#288bf0;
        color:#fff;
        display: inline-block;
        font-size:16px;
        line-height:42px;
        text-align:center;
        border-radius:5px;
        margin:20px 10px;
        &:nth-of-type(2){
          background:#dd524d;

        }
    }
}

</style>



