<template>
  <div class="my">
        
         <NavBar    fixed="true" title="" />
         <main class="main">
                 <ul class="me-list">
                      <li  class="me-list-first">
                          <router-link :to='username?"/my":"/login"'>
                               <div class="item-l">
                                     <img src="../../assets/images/userimage.png" class="item-icon"/>
                                     <span v-if="username">{{username}}</span>
                                     <span v-else></span>
                               </div>
                               <div class="item-r">
                                   <span class="iconfont icon-fangxiang"></span>
                               </div>
                          </router-link>
                       </li>
                        <li>
                          <a href="">
                               <div class="item-l">
                                      <span class="iconfont icon-fenxiang item-icon"></span>
                                     <span></span>
                               </div>
                               <div class="item-r">
                                   <span class="iconfont icon-fangxiang"></span>
                               </div>
                          </a>
                       </li>
                       
                       <li>
                          <router-link to="/instr">
                               <div class="item-l">
                                     <span class="iconfont icon-fangxiang item-icon"></span>
                                     <span></span>
                               </div>
                               <div class="item-r">
                                   <span class="iconfont icon-fangxiang"></span>
                               </div>
                          </router-link>
                       </li>
                       
                       <li>
                          <router-link to="/about">
                               <div class="item-l">
                                     <span class="iconfont icon-guanyuwomen item-icon"></span>
                                     <span></span>
                               </div>
                               <div class="item-r">
                                   <span class="iconfont icon-fangxiang"></span>
                               </div>
                          </router-link>
                       </li>
                       
                       <li>
                          <router-link to="/version">
                               <div class="item-l">
                                     <span class="iconfont icon-chakan item-icon"></span>
                                     <span></span>
                               </div>
                               <div class="item-r">
                                   <span class="iconfont icon-fangxiang"></span>
                               </div>
                          </router-link>
                       </li>
                 </ul>

                 <ul class="me-list" style="margin-top:30px">
                 
                       <li>
                          <a  href="javascript:;" style="
    justify-content: center;text-align:'center'" @click="this.$root.logout">
                            
                             <div class="item-l" >
                                    
                               </div>
                            
                          </a>
                       </li>
                 </ul>
         </main>
  </div>
</template>

<script>
import NavBar from '../../components/NavBar/NavBar.vue'
export default {
  name: 'my',
  data () {
    return {
     username:null
    }
  },
  
  mounted(){
     let   user=this.$root.isLogin();
    //  alert(user.loginName);
 
    if(user||user.loginName){
      this.username=user.loginName
    }
  },
  
  components:{
      NavBar
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "../../assets/style/base.scss";
.my{
      background: #edebe8;
}
.me-list{
  >li{
      background:#fff;
      padding:rem(30px) rem(20px);
      margin:rem(20px) 0;
    
    >a{
    display:flex;
    justify-content: space-between;
    align-items:center;
    
    .item-l{
      font-size:18px;
       display:flex;
      .iconfont{
            font-size:rem(45px);
            color:#378fe8;
      }
     align-items:center;
     span{
   
        margin-left:rem(20px) 
     }
    }
    
    .icon-fangxiang{
      color:#cacaca;
    }
  }
   &.me-list-first{
      padding-top:rem(20px);
     padding-bottom:rem(20px);
     .item-l{
     
     span{
       margin-left:rem(40px) 
     }
    }
  .item-icon{
  
    height:rem(110px);
    height:rem(110px);
      width:rem(110px);
    }

   }
  }
  
}
</style>



