<template>
  <div class="" style="height:100%">
    <NavBar    leftIcon="icon-fanhui"   fixed="true" title="" @leftActive="back()"  />
    <!-- <zpubdat/> -->
    <div class="iMagementPush" style="height:100%">
       <main class="main" style="background:#f0eff4;height:100%;">
           <div class="push-form">
               <p>
               
                <input type="text" value="" v-model="title" />
               </p>
               <p>
                    <select v-model="type">
                       <option value="0">1</option>
                        <option value="1">2</option>
                         <option value="2">3</option>
                          <option value="3">5</option>
                           <option value="5">6</option>
                    </select>
               </p>
               
               <p>
               <input type="date" value="" v-model="date" />
               </p>
                <p>
               <input type="date" value="" v-model="dateEnd" />
               </p>
               <p>
               :
               </p>
              <p>  <textarea   style="resize:none;width:100%;height:80px" placeholder="" rows="3" cols="50" value="" v-model="desc"></textarea></p>
             <div class="sj-btns">
                 <a href="javascript:;" class="sj-btn" @click="report"></a>
                 <a href="javascript:;" class="sj-btn" @click="back"></a>
             </div>
           </div>
       </main>
       <zalert v-show="isShow" :content="alertCon" @alert="alertClick"/>
  </div>
  </div>
</template>

<script>
import NavBar  from '../../components/NavBar/NavBar'
import Zalert from '../../components/Zalert/Zalert'
import { options } from '../../api/common'
export default {
  name: '',
  data () {
    return {
      isShow: false,
      alertCon: '',
      title: '',
      date: '',
      dateEnd: '',
      desc: '',
      type: ''
    }
  },
  methods: {
    back(){
      this.$router.go(-1);
    },
    inforAlert(inf) {
      this.isShow = true
      this.alertCon = inf
    },
    report() {
      if (this.title == '' || !this.title) {
        this.inforAlert('')
        return    
      }
      if (this.type == '' || !this.type) {
        this.inforAlert('')
        return    
      }
      if (this.date == '' || !this.date) {
        this.inforAlert('')
        return    
      }
      if (this.dateEnd == '' || !this.dateEnd) {
        this.inforAlert('')
        return    
      }
      if (this.desc == '' || !this.desc) {
        this.inforAlert('')
        return    
      }
      
      var pubDatUrl = "&title="+this.title+"&date="+this.date+"&taskEnd="+this.dateEnd+"&descs="+this.desc+"&taskType="+this.type
      console.log(pubDatUrl)
      this.pubAd(pubDatUrl)
    },
    pubAd(pubStr){
      this.$http.get(options.pubDate + pubStr)
      .then((res) => {
        if (res.data.result == "success") {
          console.log(res.data.msg)
          // this.inforAlert('')
          // this.back()
        }
      })
    },
    alertClick() {
      this.isShow = !this.isShow
      console.log('---11')
    }
  },
  components: {
      NavBar,
      Zalert
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import "../../assets/style/base.scss";
.push-form{
    margin:10px;
    padding:25px 10px;
    background:#fff;
    border-radius:15px;
  p{
    padding:rem(15px) 0;
    font-size:14px;
  border-bottom:1px solid  #f1f1f1;
   input[type="text"]{
       border:none;
   }
  }
}
.sj-btns{
    display:flex;
    justify-content:center;
    .sj-btn{
        height:30px;
        width:60px;
        background:#288bf0;
        color:#fff;
        display: inline-block;
        font-size:14px;
        line-height:30px;
        text-align:center;
        border-radius:5px;
        margin:20px 10px;
        &:nth-of-type(2){
          background:#dd524d;

        }
    }
}
</style>



